import CardLayout from '~/components/CardLayout.vue';
import { mapMutations } from 'vuex';

export default {
	components: {
		CardLayout
	},
	mounted () {
		this.logOut();
		this.$router.push('/');
	},
	methods: {
		...mapMutations({
			logOut: 'auth/logout'
		})
	}

};
